import React, {Component} from 'react';
import Text from './components/Text'
import './App.css';

const colors = ['#16a085', '#27ae60', '#2c3e50', '#f39c12', '#e74c3c', '#9b59b6', '#FB6964', '#342224', "#472E32", "#BDBB99", "#77B1A9", "#73A857"];

class App extends Component {
  constructor () {
    super()
    this.state = {
      text: '',
      author: '',
      quoteColor: '',
      visible: true
    }
    this.getRandomQuote = this.getRandomQuote.bind(this)
  }

  getRandomQuote() {
    fetch('https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json', {
      method: 'get',
      headers: {
          'content-type': 'application/json'
      },
      // body: JSON.stringify({
      //     id: this.state.user.id
      // })
    })
    .then(response => response.json())
    .then(data => {
        let key = Math.floor(Math.random() * (data.quotes.length - 0 + 1)) + 0;
        this.setState({
          text: data.quotes[key].quote,
          author: data.quotes[key].author,
          quoteColor: colors[Math.floor(Math.random() * (colors.length - 0)) + 0]
        }) 
        document.body.style.backgroundColor = this.state.quoteColor;
    })
    .catch(console.log)
  }

  componentDidMount() {
    this.getRandomQuote()
  }
  render () {
    const {text,author,quoteColor} = this.state
    const tweetStyle = {
      backgroundColor: quoteColor
    }
    const newQuoteStyle = {
      backgroundColor: quoteColor
    }
    const authorStyle = {
      color: quoteColor
    }
    const componentClasses = [''];
    return (
      <div className="App">
        <div id="quote-box">
          <Text text = {text} textColor={quoteColor}/>
          <div id="author" className={componentClasses} style = {authorStyle}>
            {author}
          </div>
          <div className='buttons'>
            <a 
            style={tweetStyle} 
            id="tweet-quote" 
            href={`https://twitter.com/intent/tweet?hashtags=quotes&text="${text}" ${author}`}
            target='blank'>Tweet</a>
            <div style={newQuoteStyle} id="new-quote" onClick= {this.getRandomQuote}>
              New Quote
            </div>
          </div>
        </div>
      </div>

    )
  }
}

export default App;
