import React from 'react'
// import './Text.css';

const Text = (props) => {
	const textStyle = {
		color: props.textColor,
		paddingLeft: '2em',
		paddingRight: '2em',
		paddingBottom: '2em',
		textAlign: 'left',
		minHeight: '15vh',
		maxHeight: '15vh'
	}
    return (
        <div id="text" style = {textStyle}>
          {props.text}
        </div>
    )
}

export default Text